body, html {
    height: 100%;
    width: 100%;
    margin:0;
    padding:0;
    box-sizing: border-box;
}

#root{
    height: 100%;
    width: 100%;
    margin:0;
    padding:0;
    box-sizing: border-box;
}

* {
    box-sizing: border-box;
}

.initiative{
    padding: 0 20px;
    width: auto;
    font-size: 20px;
    text-align: center;
}

.Name{
    text-transform: capitalize;
    color: cornflowerblue;
    font-weight: 500;    
}

.LabelName{
    text-align: left;
    padding: 10px 10px 25px 10px;
    font-size: 21px;
}


.steps-action{
    display: flex;
    justify-content: flex-end;
    margin-top: 30px;
}
.steps-action * {
    margin: 0 8px;
}
.steps-action :first-child {
    margin: 0 auto 0 0;
}

.row-dragging {
    background: #fafafa;
    border: 1px solid #ccc;
}
  
.row-dragging td {
    padding: 16px;
    visibility: hidden;
}

.row-dragging .drag-visible {
    visibility: visible;
}

tr:first-child {
    background-color: #e6f7ff;
}